.fancy {
  line-height: .8;
  margin-bottom: 5%;
  
  text-align: center;
   display: flex;
  justify-content: center;
  overflow: hidden;
  
}
.fancy span {
  /* display: inline-block; */
  position: relative;  
}
.fancy span:before,
.fancy span:after {
  justify-content: center;
  content: " ";
  position: absolute;
 
  border-bottom: 1px solid red;
  border-top: 1px solid black;
  top: 24px;
  width: 48%;
  /* max-width: 45%; */
  overflow: auto;
  min-width: 1vw;
}
.fancy span:before {
  right: 100%;
  margin-right: 15px;
}
.fancy span:after {
  left: 100%;
  margin-left: 15px;
}


